}
.page-id-181 .sg-text {
    width:50%;
    clear:both;
    @mixing clear;
}
.page-id-181 .sg-text a {
    font-weight:600;
    font-family:"Merriweather";
    text-decoration:underline;
    display:block;
    color:$blue;
}
.page-id-181 .sg-text a:hover {
    color:$blue_light;
}
.page-id-181 .sg-text a.purple {
    color:$purple;
}
.page-id-181 .sg-text a.purple:hover {
    color:#aa5f9a;
}
.page-id-181 h1.sg-title {
    background: $gray;
    font-size:20px;
}
