    background: $light_gray;
    color:$blue;
    font-size:16px;
}
.page-id-181 h3.sg-title {
    background: $blue_light;
}
.page-id-181 .sg-titles {
    width:100%;
}
.page-id-181 .sg-titles h1, .page-id-181 .sg-titles h2, .page-id-181 .sg-titles h3, .page-id-181 .sg-titles h4, .page-id-181 .sg-titles h5, .page-id-181 .sg-titles h6 {
    width:100%;
    margin-bottom:20px;
}
.page-id-181 .sg-buttons {
    clear:both;
    *zoom:1;
    padding:20px;
}
.page-id-181 .sg-buttons:before {
    content:'';
    display:table;
}
.page-id-181 .sg-buttons:after {
    content:'';
    display:table;
    clear:both;
}
.page-id-181 .sg-buttons:before, .page-id-181 .sg-buttons:after {
    content:" ";
