    display:block;
}
.page-id-181 .sg-buttons .button {
    margin-right:20px;
}
.page-id-181 .sg-colors {
    display:-webkit-box;
    display:-webkit-flex;
    display:-ms-flexbox;
    display:flex;
    -webkit-flex-wrap:wrap;
        -ms-flex-wrap:wrap;
            flex-wrap:wrap;
}
.page-id-181 .sg-colors li {
    -webkit-box-flex:0;
    -webkit-flex:0 0 auto;
        -ms-flex:0 0 auto;
            flex:0 0 auto;
    width:calc(99.99% * 1/4 - (30px - 30px * 1/4));
